import gql from "graphql-tag";
import { withApollo } from 'react-apollo';

const GET_POSTS = gql`
    query allPosts {
      allPosts {
        id
        title
        votes
        url
        createdAt
      }
      _allPostsMeta {
        count
      }
    }
  `
const skip = 0;
const variables = {
    first: 10,
    skip: 0
}

const App = ({client}) => {
  var dataM;
  const someQueryResult = client.query({ query: GET_POSTS, skip, variables});
  var {allPosts} = someQueryResult.then(result => console.log(result.data));
  debugger;
  return (
    <List>
      {allPosts.map((post, index) => (
        <ListItem key={post.id}>
          <ListItemContainer>
            <Num>{index + 1}. </Num>
            <A href={post.url}>{post.title}</A>
            <PostUpvoter id={post.id} votes={post.votes} />
          </ListItemContainer>
        </ListItem>
      ))}
    </List>
  )
}

export default withApollo(App);